import { _decorator, Component, Node } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('ReelController')
export class ReelController extends Component {

	@property({type: Number})
 	public round: Number = 0;
	
    start() {
    	this.trunReel(this.round, ()=>{
    		cc.log('i finished hahaha');
    	});
    }

    update(deltaTime: number) {
        
    }


    trunReel(round, callback) {

		cc.tween(this.node.children[0])
			.repeat(round, cc.tween()
				.to(0, { position: cc.v3(0, 912, 0) })
	            .to(1, { position: cc.v3(0, 0, 0) })
	            .to(1, { position: cc.v3(0, -906, 0) })
	        )
	        .call(callback())
            .start();
        cc.tween(this.node.children[1])
	        .repeat(round, cc.tween()
	       		.to(0, { position: cc.v3(0, 0, 0) })
	            .to(1, { position: cc.v3(0, -906, 0) })
	            .to(0, { position: cc.v3(0, 912, 0) })
	            .to(1, { position: cc.v3(0, 0, 0) })
            )
            .start();
    	
    }

}

